@import "News.css";

$font:Roboto Condensed,Serif;
$color-white:white;
$primary-color:#f9c803;
$color-rgba:rgba(10, 10, 10, 0.53);

body {
  margin: auto;
  /*position: relative;*/
  width: 100%;
  font-family: $font;
  letter-spacing: 0.2px;
  overflow-x: hidden;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}

.navbar {
  top: 0;
  background-color: $color-white;
  width: 100%;
}

.main_logo {
  border-style: none;
  height: 45px;
  vertical-align: inherit;
  z-index: 1;
}

.navbar-toggle {
  //clear: both;
  margin-top: 45px;
}

#Listitem > a::after {
  /*margin-top: -20px;*/
  /*margin-right: 1px;*/
  position: absolute;
  top: -2px;
  right: 100%;
  content: '';
  border-width: 46px;
  border-style: solid;
  border-color: transparent $primary-color $primary-color transparent;
}

.navbar {

  ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  li {
    display: inline-block;
  }
  a {
    color: $color-white;
    display : block;
    text-decoration: none;
    &:hover {
      background-color: transparent !important;
      text-decoration: none;
    }
    &:focus {
      background-color: transparent !important;
    }
  }
}

/*active navbar with border bottom a*/

.nav-item > a.navbar-link::after {
  content: "";
  display: block;
  height: 2px;
  background-color: transparent;
  width: 20px;
  position: absolute;
  top: 0;
  bottom: -20px;
  left: 0;
  right: 0;
  margin: auto;
}

.navbar-brand {
  position: absolute;
  top: 7px;
  left: 30px;
}

/*responsive navbar for ipad*/
@media screen and (min-width: 768px) {
  .navbar ul li a {
    height: 90px;
    line-height: 50px;
    margin-right: 10px;
    font-size: 9px;
  }
  .logo {
    img {
      width: 70px;
      height: 45px;
    }
    p {
      font-size: 10px;
      margin-top: 5px;
      color: #0099ff;
    }
  }
}

/*reponsive navbar for ipad pro and computer*/
@media screen and (min-width: 1024px) {
  .navbar ul li a {
    height: 90px;
    line-height: 50px;
    margin-right: 20px;
    font-size: 13px;
  }
  .logo {
    img {
      width: 110px;
      height: 50px;
    }
    p {
      font-size: 12px;
      margin-top: 5px;
      color: #0099ff;
    }
  }
}

/*reponsive   for type phone */
@media screen and (max-width: 415px) and (max-width: 1024px) {
  .navbar ul li a {
    height: 30px;
    line-height: 15px;
    /*margin-right: 30px;*/
    font-size: 12px;
  }
  .logo {
    img {
      width: 90px;
      height: 30px;
    }
    p {
      margin-top: 0;
      font-size: 7px;
      color: #0099ff;
    }
  }
}


